import { createRouter, createWebHistory } from 'vue-router'
import Home from '../components/HomePage.vue'
import About from '../components/AboutPage.vue'
import ProductDisplay from '../components/FirstStuff.vue'
import Unit3C from '../components/SecondStuff.vue'
import GadgetCatalog from '../components/ThirdStuff.vue'

const routes = [
    { 
        path: '/portofolio-m/', 
        component: Home 
    },
    { 
        path: '/portofolio-m/about', 
        component: About 
    },
    { 
        path: '/portofolio-m/ProductDisplay', 
        component: ProductDisplay 
    },
    { 
        path: '/portofolio-m/Unit3C', 
        component: Unit3C 
    },
    { 
        path: '/portofolio-m/GadgetCatalog', 
        component: GadgetCatalog 
    }
]

const router = createRouter({
    history: createWebHistory(),
    base: process.env.BASE_URL,
    routes,
    scrollBehavior() {
        // always scroll to top
        return { top: 0 }
    }
})

export default router;